<template>
  <div class="goodsinfo-container">
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunboList="lunboList" :isfull="false"></swiper>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="father">
      <transition name="ball" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
        <div class="ball" v-show="ballflag" ref="ball"></div>
      </transition>
    </div>
    <div class="mui-card">
      <div class="mui-card-header">{{goodsinfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            
            <del>{{goodsinfo.market_price}}</del>
            &nbsp;&nbsp;
            <span class="now_price">{{goodsinfo.sell_price}}</span>
          </p>
          <span></span>
          <numbox :shul="goodsinfo.stock_quantity"></numbox>
          <p class="buy">
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addcar" :disabled="ballflag"></mt-button>
            <!--    -->
            <!-- 1.   goodsinfo    numberbox  -->
            <!-- 2.  goodsinfo zhong  -->
            <!-- 3.   -->
            <!-- 4.      -->
          </p>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{goodsinfo.goods_no}}</p>
          <p>{{goodsinfo.stock_quantity}}</p>
          <p>{{goodsinfo.add_time | dateFormat}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="gophotochar(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="gocomment(id)"></mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import swiper from "../subcomponents/swiper";
import numbox from "../subcomponents/numbox";
export default {
  data() {
    return {
      id: this.$route.params.id,
      lunboList: [],
      goodsinfo: {},
      ballflag: false,
      flag: true,
      selectedCount: 1
    };
  },
  created() {
    this.getlunbo();
    this.getgoodsinfo();
  },
  methods: {
    getlunbo() {
      this.$http.get(`api/getthumimages/${this.id}`).then(result => {
        if (result.data.status == 0) {
          result.data.message.forEach(item => {
            item.img = item.src;
          });
          this.lunboList = result.data.message;
        }
      });
    },
    getgoodsinfo() {
      this.$http.get(`/api/goods/getinfo/` + this.id).then(result => {
        if (result.data.status === 0) {
          this.goodsinfo = result.data.message[0];
        }
      });
    },
    gophotochar(id) {
      this.$router.push({ name: "getdesc", params: { id } });
    },
    gocomment(id) {
      this.$router.push({ name: "goucomment", params: { id } });
    },
    beforeEnter(el) {
      el.style.transform = "translate(0,0)";
    },
    enter(el) {
      el.offsetWidth;
      //
      const ballposition = this.$refs.ball.getBoundingClientRect();
      const badgeposition = document
        .getElementById("badge")
        .getBoundingClientRect();

      let ballX = badgeposition.left - ballposition.left;
      let ballY = badgeposition.top - ballposition.top;

      el.style.transform = `translate(${ballX}px,${ballY}px)`;
      el.style.transition = "all 1s cubic-bezier(.4,-0.3,1,.68)";
    },
    afterEnter(el) {
      this.ballflag = !this.ballflag;
      el.style.display = "none";
      el.style.transform = `translate(0, 0)`;
      this.flag = "true";
    },
    addcar() {
      // if (this.flag) {
      // this.flag = false;
      var shuliang = parseInt(document.getElementById("test").value);

      this.ballflag = !this.ballflag;
      // }
      var goodsinfo = {
        id: this.id,
        count: shuliang,
        price: this.goodsinfo.sell_price,
        seleted: true
      };
      this.$store.commit("addTocar", goodsinfo);
    }
  },
  components: {
    swiper,
    numbox
  }
};
</script>
<style lang="scss" scoped>
.goodsinfo-container {
  background-color: #eee;
  overflow: hidden;
  // position: relative;

  .now_price {
    color: red;
    font-size: 16px;
    font-weight: bold;
  }

  .mui-card-footer {
    display: block;
    button {
      margin: 15px 0;
    }
  }
  .father {
    z-index: 99;
  }

  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    z-index: 99999;
    top: 390px;
    left: 152px;
  }
  .buy {
    margin-top: 10px;
  }
}
</style>





